import React, { useEffect, useState, ReactNode } from "react";
import { Result, Button, Spin } from "antd";
import { Block } from "../../../components";
import userApi from "../../../utils/api/user";
import { Link } from "react-router-dom";
type RenderTextInfoParamsType = {
  verified: boolean | null;
  hash: string;
  error: boolean;
};
type RenderTextInfoType = (
  params: RenderTextInfoParamsType
) =>
  | {
      status: "success" | "error" | "info" | "warning";
      title: string;
      message: string;
      extra?: JSX.Element[];
    }
  | undefined;

let renderTextInfo: RenderTextInfoType = ({ verified, hash, error }) => {
  if (hash) {
    if (verified) {
      return {
        status: "success",
        title: "!",
        message: "   ",
        extra: [
          <Link to="/signin" key="123">
            <Button type="primary">  </Button>
          </Link>
        ]
      };
    } else if (error) {
      return {
        status: "error",
        title: "",
        message: "   "
      };
    }
  } else {
    return {
      status: "success",
      title: "  !",
      message: "      "
    };
  }
};
type Props = {
  location: Location;
};
let CheckEmailInfo: React.FC<Props> = props => {
  console.log(props);
  const hash = props.location.search.split("?hash=")[1];

  let [verified, setVerified] = useState<boolean | null>(null);
  let [loading, setIsLoading] = useState(false);
  let [error, setError] = useState(false);

  let info = renderTextInfo({ verified, hash, error });
  useEffect(() => {
    if (hash) {
      userApi
        .verifyHash(hash)
        .then(async data => {
          setIsLoading(true);
          if (data.status === "success") {
            setVerified(true);
            setIsLoading(false);
          }
        })
        .catch(err => {
          setError(true);
          setIsLoading(false);
        });
    }
  }, [hash]);
  if (loading || !info) {
    return <Spin />;
  }
  return (
    <Block>
      <Result
        status={info.status}
        extra={info.extra}
        title={info.title}
        subTitle={info.message}
      />
    </Block>
  );
};

export default CheckEmailInfo;
